import React, { cloneElement, Children, forwardRef } from 'react';
import { bool, element, string } from 'prop-types';
import classnames from 'classnames';
import Icon from '../icon';

const CSS_PREFIX = 'vds-breadcrumb';
const CSS_STATE = 'vds-state';

const Breadcrumb = forwardRef(({
  children,
  className,
  current,
  separator,
  hideSeparator,
  ...remainingProps
}, ref) => (
  <li
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--active`]: current
    })}
    ref={ref}
    {...remainingProps}
  >
    {
      cloneElement(children, {
        'aria-current': current ? 'page' : undefined,
        className: classnames(`${CSS_PREFIX}-link`, Children.only(children).props.className)
      })
    }
    {hideSeparator ? undefined : separator}
  </li>
));

Breadcrumb.propTypes = {
  /**
  * @ignore
  */
  children: element,

  /**
   * @ignore
   */
  className: string,

  /**
   * Active breadcrumb
   */
  current: bool,

  /**
   * Separator
   */
  separator: element,

  /**
   * Show or hide separator
   */
  hideSeparator: bool
};

Breadcrumb.defaultProps = {
  separator: <Icon name="arrow-next" resolution="tiny" role="presentation" />
};

Breadcrumb.displayName = 'Breadcrumb';

export default Breadcrumb;
